import { Component, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import {ImghandlerProvider} from '../../providers/imghandler/imghandler';
import { UserProvider } from '../../providers/user/user';

import {HomePage} from '../../pages/home/home';




/**
 * Generated class for the ProfilepicPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-profilepic',
  templateUrl: 'profilepic.html',
})
export class ProfilepicPage {
  // photoURL: any;
  imgurl = 'https://firebasestorage.googleapis.com/v0/b/hoputademo.appspot.com/o/user_avatar_35720.png?alt=media&token=e3597d02-5429-45b7-a6cc-59158078afe9';
  moveon = true;
  constructor(public navCtrl: NavController, public navParams: NavParams, public imgservice: ImghandlerProvider, public zone: NgZone, public userservice: UserProvider, public loadingCtrl: LoadingController) {
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ProfilepicPage');
  }

  chooseimage() {
    let loader = this.loadingCtrl.create({
      content: 'Please Wait...'
    })
    loader.present();
     this.imgservice.uploadimage().then((uploadedurl: any) => {
       loader.dismiss();
      this.zone.run(() => {
        this.imgurl = uploadedurl;
        this.moveon = false;
      })
     })
  }

  updateproceed() {
    let loader = this.loadingCtrl.create({
      content: 'Please Wait while we upload your image...'
    })
    loader.present();
    this.userservice.updateimage(this.imgurl).then((res: any) => {
      loader.dismiss();
      if(res.success) {
        this.navCtrl.setRoot(HomePage);
      }
      else {
        alert(res); 
      }
    })
  }

  proceed() {
    this.navCtrl.setRoot(HomePage);
  }

}



